<template>
  <div>
    <ul>
      <li v-for="unit in GetOrderList(list)" v-bind:key="unit.id">
        <ToDoItem :todoItem="unit" @AddNewEvent="AddNewTodo"></ToDoItem>
      </li>
    </ul>
  </div>
</template>

<script>
import ToDoItem from "@/components/ToDoItem";
export default {
  name: "ToDoList",
  components: {
    ToDoItem: ToDoItem
  },
  data() {
    return {
      id: 4,
      list: [
        {
          id: 4,
          text: "一个等待被填充寂寞的空格",
          state: "new"
        },
        {
          id: 1,
          text: "我是一条已经把工作做完了的咸鱼",
          state: "finish"
        },
        {
          id: 2,
          text: "啊啊啊还有好多作业要去做！",
          state: "normal"
        },
        {
          id: 3,
          text: "明天要高考了！",
          state: "normal"
        }
      ]
    };
  },
  mounted: function() {},
  methods: {
    GetOrderList: function(list) {
      // console.log('GetOrderList');
      let normal = new Array();
      for (const x of list) {
        if (x.state != "new") {
          normal.push(x);
        }
      }
      list = new Array();
      list = list.concat(normal);
      list.push({
        id: 999,
        text: "一个等待被填充寂寞的空格",
        state: "new"
      });
      return list;
    },
    GetNewID:function()
    {
      // console.log('GetNewID');
      let newid = this.id;
      this.id++;
      return newid;
    },
    AddNewTodo:function(_text){
      // console.log('AddNewTodo');
      this.list.push({
        id:this.GetNewID(),
        text:_text,
        state:"normal"
      });
    }
  }
};
</script>

<style>
</style>



