import Vue from 'vue'
import Router from 'vue-router'
import ToDoItem from '@/components/ToDoItem'
import ToDoList from '@/components/ToDoList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ToDoList',
      component: ToDoList
    }
  ]
})



